.layout-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #ffffff;
  z-index: 100;
}

.main-wrapper {
  flex-grow: 1;
  margin-top: 60px;
  margin-bottom: 60px; 
  overflow-y: auto; 
}

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #ffffff;
  color: #fff;
  padding: 10px;
  text-align: center;
  z-index: 100;
}